body {
  font-family: 'Courier New', Courier, monospace;
}

h1,
h3 {
  font-weight: 700;
}

p,
h3 {
  line-height: 22px;
}

.Header {
  margin: 35px 0;
  position: relative; }
  .Header h1 {
    font-size: 24px; }
  .Header hr {
    border: 1px solid #6ccacc; }
  .Header--contact {
    font-weight: 700; }
    .Header--contact a {
      font-weight: 500;
      color: #6ccacc;
      text-decoration: none; }
      .Header--contact a:hover {
        font-weight: bold; }
  .Header--profile-picture {
    width: 200px;
    -webkit-transition: -webkit-transform 0.2s ease-in;
    transition: -webkit-transform 0.2s ease-in;
    transition: transform 0.2s ease-in;
    transition: transform 0.2s ease-in, -webkit-transform 0.2s ease-in;
    -webkit-transform: rotate(-6deg);
            transform: rotate(-6deg);
    position: absolute;
    top: -15px;
    right: 20px; }
    .Header--profile-picture:hover {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg); }
  .Header--summary {
    width: 45em;
    margin-top: 20px;
    font-style: italic; }
  .Header--language-select {
    position: absolute;
    top: -35px;
    left: 0; }

main .Section {
  display: -webkit-flex;
  display: flex;
  min-height: 100px;
  padding: 10px 0; }
  main .Section--left {
    -webkit-flex: 1 1;
            flex: 1 1;
    padding: 0 20px;
    position: relative;
    font-style: italic; }
    main .Section--left h3 {
      text-align: right; }
    main .Section--left:after {
      content: '';
      height: 100px;
      width: 2px;
      position: absolute;
      top: 0;
      right: 0;
      background: #6ccacc; }
  main .Section--right {
    -webkit-flex: 6 1;
            flex: 6 1;
    padding: 0 20px; }
    main .Section--right h3 {
      margin-bottom: 5px; }
    main .Section--right-job-title {
      display: -webkit-flex;
      display: flex;
      -webkit-justify-content: space-between;
              justify-content: space-between; }
      main .Section--right-job-title span {
        font-size: 14px;
        color: #989595;
        opacity: 0.75;
        font-style: italic;
        padding-left: 25px;
        line-height: 22px; }
      main .Section--right-job-title img {
        height: 20px; }
    main .Section--right-technologies {
      display: inline-block; }
      main .Section--right-technologies img {
        height: 35px;
        margin-right: 10px; }
    main .Section--right p span {
      font-weight: 700; }
    main .Section--right div {
      padding-bottom: 5px; }
      main .Section--right div:nth-last-child(1) {
        padding-bottom: 0px; }
    main .Section--right ul {
      list-style: disc inside;
      margin-bottom: 20px; }

.Footer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  background: #6ccacc;
  line-height: normal;
  line-height: initial; }
  .Footer svg {
    font-size: 22px;
    padding: 10px;
    color: gray;
    -webkit-transition: color 0.3s linear;
    transition: color 0.3s linear; }
    .Footer svg:hover {
      color: black; }

.App {
  max-width: 960px;
  padding-top: 20px;
  margin: 0 auto;
  position: relative; }

